import mongoose from 'mongoose';
mongoose.Promise = global.Promise;

const Schema = mongoose.Schema;

const UserSchema = mongoose.Schema({
	accountType:{
		type: String,
		default: 'user'
	},
	username: {
		type: String,
		required: true
	},
    createdAt: {
        type: Date
    },
	accountActive: {
		type: Boolean,
		default: true
	},

	maxParlayTeam: {
		type: Number,
		default: 6
	},
	thisWeekExtraCredit: {
		type: Number,
		default: 0
	},
	weeklyStartBalance: {
		type: Number,
		default: 20000
	},

	maxWinAmount: {
		type: Number,
		default: 10000
	},

	maxWagerAmount: {
		type: Number,
		default: 10000
	},

	minRiskAmount: {
		type: Number,
		default: 50
	},

	passcode: {
		type: String,
		default: '4321',
		required: true
	},

	password: {
		type: String,
		default: '4321',
		required: true
	},

	agent: {
		type: String,
		default: 'diamond'
	},
	superAgent: {
		type: String,
		default: 'diamond'
	}
});

const User = mongoose.model('user', UserSchema);

export default User;